import React, { Component } from 'react'
import { Layout } from 'antd';
import { withRouter } from "react-router-dom";
import Menu from "../Menu";
import Breadcrumb from "../Breadcrumb";
import "./index.less";

const { Header } = Layout;

class index extends Component {
    componentDidMount() {
        let header = document.getElementById("header");
        let before = window.scrollY;
        window.addEventListener('scroll', function (eleNum) {
            let after = window.scrollY;
            if (after > 64) {
                if (before < after) {
                    header.className = "ant-layout-header header header-affixed";
                    before = after;
                };
                if (before > after) {
                    header.className = "ant-layout-header header header-visible";
                    before = after;
                };
            }
        })
    }
    render() {
        return (
            <Header className="header" id="header">
                <div className="header-content">
                    <div className="logo" >
                        Logo
                    </div>
                    <Breadcrumb/>
                    <Menu mode="horizontal" className="header-nav" />
                </div>
            </Header>
        )
    }
}

export default withRouter(index);