import React, { Component } from 'react'
import { Layout } from 'antd';
import { GithubOutlined } from '@ant-design/icons';
import Menu from "../Menu";
import "./index.less";

const { Sider } = Layout;

export default class index extends Component {
    componentDidMount() {
        let oDiv = document.getElementById("sider-content"),
            H = 0,
            Y = oDiv;
        while (Y) {
            H += Y.offsetTop;
            Y = Y.offsetParent;
        }
        window.onscroll = function () {
            var s = document.body.scrollTop || document.documentElement.scrollTop;
            if (s > H) {
                oDiv.style = "position:fixed;top:0;";
            } else {
                oDiv.style = "";
            }
        }
    }
    render() {
        return (
            <Sider width={240} className="site-layout-background" id="sider">
                <div id="sider-content">
                    <div className="personal">
                        <div className="block">
                            <img src={require("../../assets/images/avatar.jpg")} alt="头像" />
                        </div>
                        <p className="author">LiuZheAb</p>
                        <div style={{ width: "100px", height: "40px", margin: "0 auto", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <a target="_blank" style={{ display: "flex", fontSize: "32px", color: "#000" }} rel="noopener noreferrer" href="https://github.com/LiuZheAb/"><GithubOutlined title="github" /></a>
                        </div>
                    </div>
                    <Menu mode="inline" className="" defaultOpenKeys={['gojs']}/>
                </div>
            </Sider>
        )
    }
}
