import React, { Component } from 'react'
import { Menu, } from 'antd';
import { Link } from "react-router-dom";
import "./index.less";

const { SubMenu } = Menu;

export default class index extends Component {
    render() {
        return (
            <Menu
                mode={this.props.mode}
                defaultOpenKeys={this.props.defaultOpenKeys}
                className={this.props.className}
                theme="light"
                id="menu"
            >
                <SubMenu
                    key="gojs"
                    title={<span>Go JS 入门教程</span>}
                >
                    <Menu.Item key="gojsintro"><Link to="/gojs/intro">介绍</Link></Menu.Item>
                    <Menu.Item key="gojs1"><Link to="/gojs/1">第一节 简单的GoJS demo</Link></Menu.Item>
                    <Menu.Item key="gojs2"><Link to="/gojs/2">第二节 GoJS应用中有什么</Link></Menu.Item>
                    <Menu.Item key="gojs3"><Link to="/gojs/3">第三节 使用GoJS构建Parts(Parts 和 Panels)</Link></Menu.Item>
                    <Menu.Item key="gojs4"><Link to="/gojs/4">第四节 GoJS Diatram Model</Link></Menu.Item>
                    <Menu.Item key="gojs5"><Link to="/gojs/5">第五节 使用GoJS构建Parts(Templates)</Link></Menu.Item>
                </SubMenu>
            </Menu>
        )
    }
}
