import React, { Component } from 'react'
import { List } from 'antd';
import { Link } from "react-router-dom";

const listData = [];
for (let i = 0; i < 1; i++) {
    listData.push({
        href: '/gojs',
        title: `GoJS 入门教程`,
        description:
            'GoJS 入门教程',
        content:
            'GoJS 入门教程，GoJS 入门教程，GoJS 入门教程，GoJS 入门教程，GoJS 入门教程，GoJS 入门教程',
    });
}

export default class index extends Component {
    render() {
        return (
            <List
                itemLayout="vertical"
                size="large"
                pagination={{
                    onChange: page => {
                        console.log(page);
                    },
                    pageSize: 3,
                }}
                dataSource={listData}
                renderItem={item => (
                    <List.Item
                        key={item.title}
                        extra={
                            <img
                                width={272}
                                alt="logo"
                                src="https://gw.alipayobjects.com/zos/rmsportal/mqaQswcyDLcXyDKnZfES.png"
                            />
                        }
                    >
                        <List.Item.Meta
                            title={<Link to={item.href}>{item.title}</Link>}
                            description={item.description}
                        />
                        {item.content}
                    </List.Item>
                )}
            />
        )
    }
}
